// Cup material
const material = new THREE.MeshPhysicalMaterial()
material.metalness = 0
material.roughness = 0.2
material.envMap = environmentMapTexture
material.color.set(0xe0f9ff)
material.transmission = 1
material.transparent = true
material.opacity = 0.8

// Jelly material
const jellyMaterial = new THREE.MeshPhysicalMaterial()
jellyMaterial.metalness = 0
jellyMaterial.roughness = 0
jellyMaterial.envMap = environmentMapTexture
jellyMaterial.transmission = 1
jellyMaterial.transparent = true
jellyMaterial.opacity = 0.5
jellyMaterial.color.set(0x0008ff)

