const sphere = new THREE.Mesh(
    new THREE.SphereGeometry(0.5, 32, 32),
    material 
)

/**
 * Model
 */

// Load jelly
const gltfLoader = new GLTFLoader()
let model = null
let cup = null

gltfLoader.load(
    './model/glTF/jelly.gltf',
    (gltf) => {
        model = gltf.scene
        scene.add(model)
        model.position.y = 0.35
        model.scale.set(0.33, 0.33, 0.33)

        // Loop through each mesh in the model and set the material
        model.traverse((child) => {
            if (child.isMesh) {
                child.material = jellyMaterial
                }
            }
        )  
    }
)

// Load cup
gltfLoader.load(
    './model/glTF/cup.gltf',
    (gltf) => {
        cup = gltf.scene
        scene.add(cup)
        cup.position.y = -0.8
        cup.scale.set(0.35, 0.35, 0.35)

        // Loop through each mesh in the model and set the material
        cup.traverse((child) => {
            if (child.isMesh) {
                child.material = material
                }
            }
        )  
    }
)

/**
 * Raycaster
 */
// Create raycaster to check the intersection of the mouse and the model
const raycaster = new THREE.Raycaster()
let currentIntersect = null

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 1
camera.position.z = 2
scene.add(camera)

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Mouse
 */
// Create mouse event to calculate the position of the mouse and use them for raycaster object
const mouse = new THREE.Vector2()

window.addEventListener('mousemove', (event) =>
{
    mouse.x = event.clientX / sizes.width * 2 - 1
    mouse.y = - (event.clientY / sizes.height) * 2 + 1
})

raycaster.setFromCamera(mouse, camera)

